/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.edgtf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 40px;

	.edgtf-widget-title {
		margin: 0 0 21px;
		
		a {
			color: inherit;
		}

		&:after {
			content: '';
			position: relative;
			display: block;
			margin-top: 10px;
			width: 62px;
			height: 9px;
			background: transparent url(../img/beer-stroke.svg) no-repeat 0;
			background-size: 62px 9px;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .edgtf-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

	    .edgtf-widget-title {
		    margin: 0 0 13px;
	    }

        form {
            @include edgtfRelativeHolderLayout();
            clear: both;

            > div {
                @include edgtfRelativeHolderLayout();
	            border: 1px solid $default-border-color;
                box-sizing: border-box;
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            font-family: inherit;
            font-weight: 400;
            font-size: 15px;
            line-height: 24px;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include edgtfTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 44px);
	        height: 44px;
            padding: 10px 10px 10px 16px;
	        color: #bcbcbc;
	        
	        &:focus {
		        color: $default-heading-color;
		        background-color: #f5f0e5;
	        }
	
	        @include edgtfPlaceholder {
		        color: inherit;
	        }
        }

        button {
            width: 40px;
	        height: 40px;
	        margin: 2px 2px;
            padding: 0;
            font-size: 16px;
            color: #fff;
	        background-color: $first-main-color;
            cursor: pointer;
            @include edgtfTransition(color .2s ease-in-out, background-color .2s ease-in-out);

            &:hover {
                background-color: $default-heading-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {
		margin: 0 0 29px;

	    .edgtf-widget-title {
		    margin: 0 0 18px;
	    }

	    ul {
		    position: relative;

            li {
                margin: 0;
                padding: 4px 0 4px 14px;
	            line-height: 28px;

	            &:before {
		            content: '·';
		            display: block;
		            position: absolute;
		            left: -2px;
		            font-size: 35px;
		            color: $first-main-color;
	            }

                ul {
                    margin: 4px 0 0 10px;
                    padding: 0;

                    li {

                        &:last-child {
                            padding: 4px 0 0 14px;
                        }
                    }
                }
            }
        }
    }
	
	&.widget_categories {
		
		ul {
			
			li {
				color: $default-heading-color;
			}
		}
	}

    &.widget_recent_entries {
	
	    .edgtf-widget-title {
            margin: 0 0 20px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
	                font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .edgtf-widget-title {
            margin: 0 0 12px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 6px 10px 0;
            padding: 0 10px;
            font-family: inherit;
            font-size: 14px !important;
            line-height: 28px;
	        text-transform: lowercase;
            color: $default-heading-color;
            background-color: inherit;
	        border: 1px solid transparent;
            @include edgtfTransition(color .2s ease-out, background-color .2s ease-out, border-color .2s ease-out);

            &:hover {
                border-color: $first-main-color;
            }
        }
    }
	
	&.widget_media_image {
		margin-bottom: 20px;
		
		a, img {
			display: block;
		}
	}
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.edgtf-page-header,
.edgtf-top-bar,
.edgtf-sticky-header,
.edgtf-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.edgtf-side-menu,
.edgtf-page-footer {

    .widget {
	    color: #fff;
	
	    &:last-child {
		    margin: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
            margin: 0;
	        color: #fff;
        }
	
	    .edgtf-widget-title {
		    color: #fff;
		    margin: 0 0 27px;
	    }
	
	    a {
		    color: #fff;
		
		    &:hover {
			    color: $first-main-color;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: #b1b2b2;
        }

        table tbody th {
            border-right-color: #b1b2b2;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #fff;
		    }
	    }
	
	    &.widget_rss {
		
		    .edgtf-footer-widget-title {
			
			    .rsswidget {
				    color: #fff;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

	        input,
	        button {
	            color: #fff;
	        }

	        input {

		        @include edgtfPlaceholder {
			        color: #bcbcbc;
		        }
	        }

	        button {

	            &:hover {
		            color: #111;
	                background-color: #fff;
	            }
	        }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #b1b2b2;
                border-color: #b1b2b2;

                option {
                    background-color: #363636;
                }
            }
        }
	
	    &.widget_categories {
		
		    ul {
			
			    li {
				    color: #b1b2b2;
			    }
		    }
	    }

        &.widget_tag_cloud {

            a {
                color: #b1b2b2;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
}

.edgtf-page-footer {

	.widget {
		color: #fff;

		.edgtf-widget-title {
			margin: 0 0 35px;

			&:after {
				display: none;
			}
		}

		p {
			color: #fff;
		}

		a {
			color: #fff;

			&:hover {
				color: $first-main-color;
			}
		}

		&.widget_nav_menu {

			ul {
				list-style: none;

				li {
					padding: 0;

					&:before {
						content: " ";
					}
				}
			}
		}
	}

	.edgtf-footer-bottom-holder {

		.widget {
			color: #949494;
            font-size: 13px;

			p {
				color: #949494;
			}

			a {
				color: #fff;

				&:hover {
					color: $first-main-color;
				}
			}
		}
	}
}

.edgtf-top-bar {

	a {
		color: #969696;

		&.edgtf-header-cart {
			color: #fff;
		}

		&:hover {
			color: $first-main-color;
		}
	}

	.edgtf-icon-widget-holder {

		.edgtf-icon-element {
			color: #fff;
		}

		.edgtf-icon-text {
			color: rgba(#fff, 0.5);
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */