@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 84px 0 20px;
    }
	
	.edgtf-comment {
		padding: 26px 0 43px;
		border-bottom: 1px solid #ddd;

		&.edgtf-pingback-comment {
			
			.edgtf-comment-text {
				padding: 0;
			}
		}
	}

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 0;
            padding: 0;
        }

        > li:first-child {

	        > .edgtf-comment {
		        padding-top: 0;
	        }
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .edgtf-comments-title {
        margin-bottom: 24px;

        > * {
            margin: 0;
        }
    }

    .edgtf-comment-image {
        position: relative;
        display: block;
        top: 22px;
        left: 0;
        width: 80px;
        height: 80px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 110px;
        min-height: 80px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 5px 0 6px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 12px;
	        font-weight: 700;
	        line-height: 1;
	        letter-spacing: .12em;
	        text-transform: uppercase;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            margin: 0 0 28px;
            font-size: 12px;
	        line-height: 1;
	        color: $first-main-color;
        }

        .edgtf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .edgtf-comment-form-inner {
        padding: 50px 0 78px;
    }
    
    .comment-reply-title {
        margin: 0 0 15px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }

	#respond {

		textarea {
			
			@media only screen and (min-width: $ipad-landscape-plus-pixel) {
				border-bottom: 0;
			}
		}

		#author {
			
			@media only screen and (min-width: $ipad-landscape-plus-pixel) {
				border-right: 0;
			}
		}
	}
}

@include ipad-portrait {

    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .edgtf-comment-text {
	        
            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .edgtf-comment-holder {
	
	    .edgtf-comments-title {
		    margin-bottom: 44px;
	    }

        .edgtf-comment-image {
            float: none;
	        top: 0;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
	display: block;
	max-width: 584px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
	}
	
	&.edgtf-disable-ipad-bottom-border {
		
		@include ipad-landscape {
			border-bottom: 0;
		}
	}
	
	&.edgtf-disable-bottom-border {
		
		@media only screen and (min-width: $ipad-landscape-plus-pixel) {
			border-bottom: 0;
		}
	}
	
	&.edgtf-disable-right-border {
		
		@media only screen and (min-width: $ipad-landscape-plus-pixel) {
			border-right: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include edgtfButtonDefaultStyle();
	@include edgtfButtonMediumParams();
	@include edgtfButtonSolidColor();
	margin: 20px 0 0;
	cursor: pointer;
	
	&:hover {
		@include edgtfButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.edgtf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ===========================================================================
   #Age verification styles - start
   ===========================================================================*/
.age-gate-wrapper {
	background-color: #191410;

	.age-gate-form {
		max-width: 800px;
		background-color: #191410;

		h1,
		.age-gate-subheading,
		.age-gate-message {
			color: #fff;
		}

		.age-gate-message {
			margin: 10px 0 21px;
		}

		.age-gate-subheading {
			margin: 10px 0 20px;
		}

		.age-gate-label {
			display: none;
		}

		li {
			margin: 0 -2px;
		}

		.age-gate-remember-wrapper {
			display: none;
		}

		.age-gate-input {
			max-width: 87px;

			@include edgtfPlaceholder {
				color: #fff;
			}

			&:focus {
				background-color: transparent;
				color: #fff;
			}
		}

		.age-gate-form-elements,
		.age-gate-submit {
			display: inline-block;
		}
	}

	.age-gate-submit {
		@include edgtfButtonDefaultStyle();
		@include edgtfButtonMediumParams();
		@include edgtfButtonSolidColor();
		margin: 0 0 0 28px;
		padding: 12px 31px;
		color: $default-heading-color;
		background-color: $first-main-color;
		cursor: pointer;

		&:before {
			position: absolute;
			display: block;
			content: " ";
			top: -5px;
			left: -5px;
			width: calc(100% + 8px);
			height: calc(100% + 8px);
			border: 1px solid #fff;
		}
	}
}

/* ===========================================================================
   #Age verification styles - end
   ===========================================================================*/


/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.edgtf-slider-is-moving {
		
		.owl-item {
			
			.edgtf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.edgtf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 23px); // 23 is is half height of pagination control + top margin
                @include edgtfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include edgtfTransform(translateY(-50%));
			@include edgtfTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 30px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 20px;
		}
		
		.owl-next {
			right: 20px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 26px 0 0;
		
		.owl-dot {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: 10px;
			height: 10px;
			margin: 0 13px;
			line-height: 1;
			border-radius: 3em;
			background-color: $first-main-color;
			box-sizing: border-box;
			
			span {
				position: absolute;
				content: '';
				left: -6px;
				top: -6px;
				width: 100%;
				height: 100%;
				padding: 5px;
				opacity: 0;
				border: 1px solid #000;
				border-radius: 50%;
				box-sizing: content-box;
				@include edgtfTransform(scale(0.5));
				@include edgtfTransitionTransform(.2s cubic-bezier(.21,.6,.44,2.18), opacity .3s ease);
			}
			
			&:hover,
			&.active {
				span {
					opacity: 1;
					@include edgtfTransform(scale(1));
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .edgtf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .edgtf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            img {
                border: 2px solid transparent;
                @include edgtfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.edgtf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .edgtf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .edgtf-wrapper {
        padding: 0 !important;
    }
    
    .edgtf-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.edgtf-content {
			height: calc(100vh - 32px);
		}
	}

    .edgtf-content-inner {
	    @include edgtfTableLayout();
    }

    .edgtf-page-not-found {
	    @include edgtfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .edgtf-404-title {
            margin: 0;
			color: #fff;
        }

	    .edgtf-404-subtitle {
            margin: 35px 0 0;
			color: #fff;
        }
	    
	    .edgtf-404-text {
		    margin: 19px 0 0;
			color: #fff;
	    }

        .edgtf-btn {
            margin: 38px 0 0;
	
	        &.edgtf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }

    @include ipad-portrait {

        .edgtf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    width: 35px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

	&:hover {
		.edgtf-border-holder {
			top: -1px;
			left: -1px;
			padding: 0;
			@include edgtfTransition(padding .2s ease, top .2s ease, left .2s ease);
		}
	}

    &.off {
        opacity: 0;
        visibility: hidden;
        @include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include edgtfTransition(opacity 0.3s ease);
    }

    .edgtf-icon-stack {
        position: relative;
        display: block;
        width: 35px;
        height: 35px;
        line-height: 38px;
        font-size: 13px;
        text-align: center;
        color: $default-heading-color;
        background-color: $first-main-color;
        overflow: hidden;

        &:hover {
        }
    }

    .edgtf-border-holder {
        position: absolute;
        content: '';
        top: -6px;
        left: -6px;
        width: 100%;
        height: 100%;
		padding: 5px;
        border: 1px solid #777;
        box-sizing: content-box;
		@include edgtfTransition(padding .2s cubic-bezier(.21,.6,.44,2.18), top .2s cubic-bezier(.21,.6,.44,2.18), left .2s cubic-bezier(.21,.6,.44,2.18));
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {
	
    .edgtf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Custom Font - start
   ========================================================================== */
@font-face {
	font-family: 'Volkorn';
	src: url('../fonts/vollkorn-bold.ttf')  format('truetype');
}

/* ==========================================================================
   #Custom Font - end
   ========================================================================== */

/* ==========================================================================
   #Subscribtion Form - start
   ========================================================================== */

.edgtf-subscription-form {
    max-width: 260px;

    .wpcf7-form-control-wrap {
        display: inline-block;
        width: calc(100% - 40px);
        height: 40px;
        background: transparent;
    }

    input[type=email] {
        background-color: transparent;
        line-height: 40px;
        height: 40px;
        outline: 0;
        margin: 0;
        font-size: 11px;
        font-style: normal;
        font-weight: 400;
        text-transform: none;
        -webkit-appearance: none;
        border: 1px solid #cccccc;
        color: #949494;
    }

    .edgtf-submit-icon {
        position: relative;
        float: right;
        display: inline-block;
        width: 40px;
        height: 40px;

        &:hover {

            &:before {
              color: $first-main-color;
            }

            input.wpcf7-form-control.wpcf7-submit {
              background-color: #fff;
            }
        }

        .ajax-loader {
           display: none;
        }

        &:before {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            content: "\45";
            font-family: ElegantIcons;
            font-size: 20px;
            line-height: 40px;
            text-align: center;
            color: #fff;
            cursor: pointer;
            z-index: 1;
            @include edgtfTransition(color .3s ease);
        }

        input.wpcf7-form-control.wpcf7-submit {
            height: 100%;
            width: 100%;
            padding: 0;
            margin-top: 0;
            margin-left: 0;
            text-align: center;
            color: #fff;
            background-color: $first-main-color;
            border: 1px solid $first-main-color;
	        border-radius: 0;
            outline: 0;
            cursor: pointer;
            -webkit-appearance: none;
            box-sizing: border-box;
            font-size: 0;
            @include edgtfTransition(background-color .3s ease, border .3s ease);
        }

    }
}