/* ==========================================================================
   Icon widget style - begin
   ========================================================================== */

.edgtf-ribbon {
	position: relative;
	display: block;
	top: -20px;
	padding: 20px 18px 19px;
	width: 188px;
	background: rgba(0,0,0,.91);
	border-left: 1px solid rgba(#fff, 0.35);
	border-right: 1px solid rgba(#fff, 0.35);
	text-align: center;
	box-sizing: border-box;
	opacity: 0;
	@include edgtfTransform(translateY(-50px));
	@include edgtfTransitionTransform(.3s cubic-bezier(.21,.6,.44,2.18), opacity .3s ease);

	&.edgtf-ribbon-appeared {
		opacity: 1;
		@include edgtfTransform(translateY(0));
	}

	&:not(.edgtf-active) {
		cursor: pointer;
		@include edgtfTransform(translateY(0));
		@include edgtfTransitionTransform(.2s cubic-bezier(.21,.6,.44,2.18));

		&:hover {

			@include edgtfTransform(translateY(10px));
		}
	}

	.edgtf-ribbon-opener {
		padding: 49px 0 0;

		.edgtf-ribbon-title {
			color: $first-main-color;
			text-transform: uppercase;

			> span {
				-webkit-animation: blinky .7s ease-in infinite;
				-moz-animation: blinky .7s ease-in infinite;
				animation: blinky .7s ease-in infinite;

			}
		}
	}


	.edgtf-ribbon-content {
		display: none;
		margin: 38px 0 0;
		color: #c6c6c6;

		a {
			color: #fff;

			&:hover {
				color: $first-main-color;
			}
		}
	}

	&:before,
	&:after {
		position: absolute;
		content: " ";
		border-style: solid;
	}

	&:before {
		border-width: 32px 96px 0;
		border-color: rgba(#fff, 0.35) transparent;
		bottom: -32px;
		left: -4px;
		z-index: 0;
	}

	&:after {
		border-width: 30px 91px 0 91px;
		border-color: #000 transparent;
		bottom: -30px;
		left: 0;
		z-index: 1;
	}
}

.edgtf-header-divided .edgtf-page-header .edgtf-vertical-align-containers .edgtf-position-left .edgtf-divided-left-widget-area .edgtf-divided-left-widget-area-inner > .edgtf-ribbon {
	display: block;
}

@-webkit-keyframes blinky {

	0%,100% {
		opacity:1;
	}

	50% {
		opacity: 0.5;
	}
}

@-moz-keyframes blinky {

	0%,100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

@-o-keyframes blinky {

	0%,100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

@keyframes blinky {

	0%,100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

/* ==========================================================================
   Icon widget style - end
   ========================================================================== */